import React from 'react';
import { Link } from "react-router-dom";
import { TopNavGl } from './components/TopNavGl';
import photo_1 from './images/pattern/stroke_9_0.png';
import photo_2 from './images/pattern/stroke_9_1.png';
import { FooterGl } from './components/FooterGl';
// import CalendarGl from './components/CalendarGl';
import ModalsGl from "./components/ModalsGl";

class Reserve_Identify extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0)
        // console.log(CalendarGl.context)
        // document.querySelector('.frsTrainDateGl').innerHTML(window.reserve_start)
    }
    render() {
        return (
            <div>
                <header>
                    <TopNavGl />
                </header>
                <main>
                    <article className="reserveGlRs">
                        <section>
                            <div className="flex wrap">
                                <h1 className="selfEnd">Reservation</h1>
                                <img src={photo_1} className="imgFstGlRs" alt="" />
                                <img src={photo_2} className="imgSecGlRs" alt="" />
                            </div>
                            <div className="stepRndGlRs flex justEnd alignStart">
                                <div className="column roundDotGlRs">
                                    <i className="flex"><h4 className="lightgrey">1</h4></i>
                                    <h6>DATE &amp; TIME</h6>
                                    <p className="frsTrainDateGl"></p>
                                </div>
                                <div className="column roundLinGlRs">
                                    <i className="flex"><h4 className="lightgreen">2</h4></i>
                                    <h6 className="lightgreen">MEMBER</h6>
                                    <p>Identify</p>
                                </div>
                                <div className="column roundDotGlRs">
                                    <i className="flex"><h4 className="lightgrey">3</h4></i>
                                    <h6>CONFIRMATION</h6>
                                </div>
                            </div>
                        </section>
                    </article>
                    <section>
                        <h3>Don't we know You?</h3>
                        <form id="formCheckUserRs" className="flex" method="post" action="/en/clients/quick_reserve?reserve_start=2019-11-14T19%3A00%3A00%2B02%3A00">
                            <input type="hidden" name="client[reserve_start]" id="client_reserve_start" />
                            <div className="column">
                                <h4 className="darkblue">Yes, its not my first time</h4>
                                <div className="logoGlRs column">
                                    <fieldset className="fieldsetGl alignLf">
                                        <legend>E-mail address:</legend>
                                        <input className="inputFiedGl" placeholder="Enter e-mail address" required="required" type="text" name="client[email]" id="client_email" />
                                    </fieldset>
                                    <fieldset className="fieldsetGl fieldPartGl alignLf">
                                        <legend>Password:</legend>
                                        <input className="inputFiedGl" placeholder="Enter password" required="required" type="password" name="client[password]" id="client_password" />
                                    </fieldset>
                                    <button type="submit" form="formCheckUserRs" className="logoSubGl">Login</button>
                                </div>
                            </div>
                            <div className="column justCtr shadow">
                                <h4 className="darkblue">No, but its nice to meet you </h4>
                                <Link to="/reserve_member" className="buttBanGreen">Sign up</Link>
                            </div>
                        </form>
                    </section>
                </main>
                <FooterGl />
                <ModalsGl pathname={this.props.history.location.pathname} />
            </div>
        );
    }
}
export default Reserve_Identify;