import React from 'react';
import { TopNavGl } from './components/TopNavGl';
import photo_1 from './images/pattern/stroke_9_0.png';
import photo_2 from './images/pattern/stroke_9_1.png';
import photo_3 from './images/icons/icon_9_2.png';
import ReserveFormGl from './components/ReserveFormGl';
import { FooterGl } from './components/FooterGl';
import ModalsGl from "./components/ModalsGl";

class Reserve_Member extends React.Component {
    componentDidMount() {
        window.scrollTo(0, 0)
        function setPersRes(elem) {
            if (!elem) return
            let persent = new Date().getDate()
            console.log(persent)
            elem.innerHTML = persent * 3 + '%'
        }
        setPersRes(document.querySelector('.persntResGl'))
    }
    render() {
        return (
            <div>
                <header>
                    <TopNavGl />
                </header>
                <main>
                    <article className="reserveGlRs">
                        <section>
                            <div className="flex wrap">
                                <h1 className="selfEnd">Reservation</h1>
                                <img src={photo_1} className="imgFstGlRs" alt="" />
                                <img src={photo_2} className="imgSecGlRs" alt="" />
                            </div>
                            <div className="stepRndGlRs flex justEnd alignStart">
                                <div className="column roundDotGlRs">
                                    <i className="flex"><h4 className="lightgrey">1</h4></i>
                                    <h6>DATE &amp; TIME</h6>
                                    <p className="frsTrainDateGl">14.11 19:00</p>
                                </div>
                                <div className="column roundLinGlRs">
                                    <i className="flex"><h4 className="lightgreen">2</h4></i>
                                    <h6 className="lightgreen">MEMBER</h6>
                                    <p>Identify</p>
                                </div>
                                <div className="column roundDotGlRs">
                                    <i className="flex"><h4 className="lightgrey">3</h4></i>
                                    <h6>CONFIRMATION</h6>
                                </div>
                            </div>
                        </section>
                    </article>
                    <section className="containerFormRs flex alignStart">
                        <article className="sideLeftRs column justBet alignLf">
                            <div className="icon_9_2">
                                <img src={photo_3} alt="" />
                            </div>
                            {/* <div className="roundPersGl flex">
                                    <i className="flex">
                                        <h3 className="persntResGl darkblue"> </h3>
                                    </i>
                                </div> */}
                            <p>We know so little about you,<br />please keep going.<br />Youre almost there!</p>
                            <p>We know so little about you,<br />please keep going.<br />Youre almost there!</p>
                        </article>
                        <article className="sideRightRs">
                            <hgroup className="alignLf">
                                <h3>New to 35FIT ?<br />Start training quicker</h3>
                                <p className="lightgrey">As one of our main focus is save your time, please fill short information about you, so you can get wristband quicker and start measuring on our Milonizer</p>
                            </hgroup>
                            <ReserveFormGl>
                                <div>
                                    <button className="sendButtRs buttBanGreen" type="submit" form="profileFormGl">Continue</button>
                                </div>
                            </ReserveFormGl>
                        </article>
                    </section>
                </main>
                <FooterGl />
                <ModalsGl pathname={this.props.history.location.pathname} />
            </div>
        );
    }
}
export default Reserve_Member;